.shaketest-enter{-webkit-animation:0.8s Shake ease;animation:0.8s Shake ease}.shaketest-exit{-webkit-animation:0.5s Shake ease;animation:0.5s Shake ease}.front-page{height:86vh;display:flex;flex-direction:column;justify-content:center;align-items:center;position:relative;text-align:center}.front-page__title-wrapper,.front-page__job-wrapper{display:flex;justify-content:center}.front-page__title{margin:0;font-size:4.8em}.front-page__title--orange{color:#F56F36}.front-page__title-wrapper{margin:0 0 2rem 0}.front-page__job{margin:0;font-size:3.6em;color:#8D8D8D;font-weight:300;font-style:italic}.front-page__chevron{margin-top:15vh;cursor:pointer;right:0;bottom:15;color:white}@media (max-width: 767.98px){.front-page__title-separator{display:none}}

.about{padding-top:6vh;margin-bottom:20vh}.about__text{font-size:1.8em}.about__text-wrapper{width:60vw}.about__curriculum{align-self:center;border-radius:50%;height:4.5rem;width:4.5rem;color:#8D8D8D;background-color:white;display:flex;justify-content:center;align-items:center;font-size:2.8em;margin:0 auto;cursor:pointer;-webkit-transition:0.1s;transition:0.1s}.about__curriculum:hover{background-color:#F56F36;color:white}.about__wrapper{display:flex;flex-wrap:wrap}@media (max-width: 767.98px){.about{margin-bottom:10vh}.about__text-wrapper{width:100vw}.about__curriculum{font-size:3.8em;margin-top:3rem}}

.skills{position:relative;background-color:#2D2D2D;margin-top:5vh;padding:3rem 0;width:100vw}.skills::before{position:absolute;content:" ";display:block;top:-1rem;height:3rem;-webkit-transform:rotate(1deg);transform:rotate(1deg);width:100vw;background-color:#2D2D2D;overflow:hidden}.skills::after{position:absolute;content:" ";display:block;bottom:-1rem;height:3rem;-webkit-transform:rotate(1deg);transform:rotate(1deg);width:100vw;background-color:#2D2D2D;overflow:hidden}.skills__icons{display:flex;justify-content:center;flex-wrap:wrap;padding:0;list-style:none;margin-bottom:5vh}.skills__item{margin:1rem 3rem}

.gallery{margin-top:15vh;margin-bottom:10vh}.gallery__title{margin-bottom:5vh}img{max-width:80% !important;max-height:80vh !important}.masonry__grid{display:flex;margin-left:-1.5rem;width:auto}.masonry__column{padding-left:1.5rem;background-clip:padding-box}.masonry__column div{margin-bottom:1.5rem}.masonry__item{position:relative;-webkit-transition:all .15s ease-out;transition:all .15s ease-out}.masonry__item:hover .masonry__item-btn{-webkit-transform:scale(1);transform:scale(1);opacity:1}.masonry__item img{max-width:100% !important}.masonry__item-content{position:absolute;width:100%;height:100%;display:flex;justify-content:center;align-items:center}.masonry__item-btn{border-radius:50%;height:4.5rem;width:4.5rem;background-color:#1D1D1D;color:#F56F36;display:flex;justify-content:center;align-items:center;cursor:pointer;margin:0 2rem;font-size:2.2em;-webkit-transition:all .15s ease-out;transition:all .15s ease-out;opacity:0;-webkit-transform:scale(0.2);transform:scale(0.2)}.masonry__item-btn:hover{color:white}

.carouselFooter{width:100vw;display:flex;justify-content:center;align-items:center;margin-top:1rem}.carouselFooter__wrapper{background-color:#1D1D1D;border-radius:0.5rem;display:flex;justify-content:center;align-items:center;max-width:80vw}.carouselFooter__title{font-size:2.4em;margin:0;text-transform:uppercase}.carouselFooter__title-wrapper{padding:1rem;margin-right:1rem}.carouselFooter__context{margin:0;font-size:1.2em;text-transform:lowercase;color:#F56F36}.carouselFooter__description{max-width:50vw;font-size:1.2em;padding-right:1rem}.carouselFooter__description-wrapper{display:flex}.carouselFooter__more{display:inline-block;padding:0.5rem 3rem;border:0.16em solid #F56F36;box-sizing:border-box;text-decoration:none;text-transform:uppercase;-webkit-transition:all 0.15s;transition:all 0.15s;color:#F56F36;font-size:1.2em;margin:0 1rem}.carouselFooter__more:hover{background-color:#F56F36;color:white}@media (max-width: 600.98px){.carouselFooter__wrapper{padding-bottom:1rem;flex-wrap:wrap}.carouselFooter__title-wrapper{padding:1rem 0 0.5rem 0}.carouselFooter__more{margin-top:0.5rem}}

.footer{position:relative;overflow:hidden;display:flex;justify-content:space-between;align-items:flex-end;flex-wrap:wrap;padding:2.5rem 2.5rem 1rem 2.5rem}.footer__copyright{margin-left:10rem}.footer__social{z-index:2;display:flex;list-style-type:none;padding:0;margin:0}.footer__social-wrapper{display:flex;align-items:flex-end}.footer__social-item{margin:0 1rem;color:#8D8D8D;font-size:6em}.footer__social-item a{color:inherit;-webkit-transition:all .15s ease-out;transition:all .15s ease-out}.footer__social-item a:hover{color:#F56F36}.footer__tags{z-index:2}@media (max-width: 1025px){.footer{justify-content:start}.footer__copyright{margin:0;order:2;margin-bottom:5vh}.footer__social-wrapper{order:1;margin-bottom:5vh;width:100%}.footer__tags{z-index:2;order:3;width:100%}}

